
import { PropType, defineComponent } from "vue";
import formatInput from "@/shared/components/FormatInput/FormatInput.vue";
import PhoneInput from '@/shared/components/PhoneInput/PhoneInput.vue';
import Timer from '@/shared/components/Timer/Timer.vue'
import { COMMON_KEY_VALUE } from '@/shared/constants/common.const'
import { IbUserDetail } from "@/shared/type/CIB20100311.type";
import { CIB20100222_REQ_BODY, CIB20100222_RES } from "@/shared/type/CIB20100222.type";
import { CIB20100223_REQ_BODY, CIB20100223_RES } from "@/shared/type/CIB20100223.type";
import { CIB20100333_REQ_BODY, CIB20100333_RES } from "@/shared/type/CIB20100333.type";
import { modalController } from "@ionic/vue";
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";

export default defineComponent({
	name: "cib201003ss03",
	components: {
		Timer,
		formatInput,
		PhoneInput
	},
	props: {
		data: {
			type: Object as PropType<IbUserDetail>,
			required: true,
		},
		userId: {
      type: String,
      required: true
    },
	currentTab: {
      type: String,
      required: true,
    },
	},

	data() {
		const userID = this.$route.query.userID;
		const approvalTap = this.$route.query.tab;
        // const customerNo = this.$route.query.customerNo;
		return {
			userID,
			approvalTap,
			customerNo:"",
			securityMediaNo:"",
			securityMediaTypeCode:"",
			customerEmail:"",
			customerPhoneNumber:"",
			authenticationOTPSMSCode: {
				authenticationCode:'',
				authTransactionDate:'',
				authTransactionSeqNo:'',
            },
			COMMON_KEY_VALUE,
			isEditable: false,
			isInquiredInput: false,
			isCountDown: false,
			isEditableBtnSendCode: true,
			isComfirmOTPSMSCodeButton: true,
			isWrongPhoneNumberFormat: false,
			isWrongCustomerEmailFormat: false,
			isAuthenticationCode: false,
			isNotComfirmOTPCode: false,
			isIncorrectOTPCode: false,
			isCorrectOTPCode: false,
			isDisableComfirmOTPCode: true,
			isEmptyAuthenticationCode: false,
			approvalRes: {} as any,
			operationMode: 'normal' as screenMode,
			tempData:{
				securityMediaNo:'',
				customerPhoneNumber:'',
				customerEmail:'',
				securityMediaTypeCode:''
			}
		};
	},

	watch: {
		data(customerData: IbUserDetail) {
			if(this.approvalType == ''){
				this.customerNo = customerData.userInfo.customerNo
				this.securityMediaNo=  customerData.userInfo.securityMediaNo;
				this.customerPhoneNumber=  customerData.userInfo.securityMediaNo;
				this.customerEmail = customerData.userInfo.email;
				this.securityMediaTypeCode= customerData.userInfo.securityMediaTypeCode;
				this.tempData.securityMediaNo =  customerData.userInfo.securityMediaNo;
				this.tempData.customerPhoneNumber =  customerData.userInfo.securityMediaNo;
				this.tempData.customerEmail = customerData.userInfo.email;
				this.tempData.securityMediaTypeCode = customerData.userInfo.securityMediaTypeCode;
			}else{
				if(this.approvalTap == 'CIB201003SS03'){
					this.$approval.getAuthorizationDetail().then( (authDetail: APM20200113_RES) => {
						let authDetailParsed = JSON.parse(authDetail.body.requestContent);
						this.securityMediaTypeCode = authDetailParsed.securityMediaTypeCode
						this.customerNo = authDetailParsed.customerNo;
						this.customerEmail = authDetailParsed.email;
						this.securityMediaNo = authDetailParsed.securityMediaNo;
						this.approvalRes = authDetail.body;
					})
					this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
				}
			}
		},

		securityMediaNo(emailValue){
			if(this.securityMediaTypeCode == '01' && emailValue){
				this.isWrongPhoneNumberFormat = false;
			}
		},

		customerEmail(customerEmailvalue){
			if(customerEmailvalue){
				this.isWrongCustomerEmailFormat = false;
			}
		},

		securityMediaTypeCode(newValue){
			if(this.approvalType == ''){
				if(newValue == '01'){
					this.securityMediaNo = this.customerPhoneNumber;
				}else{
					this.securityMediaNo = this.customerEmail;
				}
			}
		},

		'authenticationOTPSMSCode.authenticationCode'(newAuthenticationCode){
			if(newAuthenticationCode){
				this.isNotComfirmOTPCode = false;
			}
		},

		isEditable(isAllowTabChange) {
			this.$emit('isBeingEdited', isAllowTabChange)
		}
	},

	computed:{
		approvalType(): string {
			return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
		},
    },

	methods: {
		onClickOperation(btn: string) {
			switch (btn) {
				case "edit":
					this.isEditable = true;
					this.isEditableBtnSendCode = false;
					break;

				case "cancel":
					this.isCountDown = false;
					this.isCorrectOTPCode = false;
					this.authenticationOTPSMSCode.authenticationCode = '';
					this.isComfirmOTPSMSCodeButton = true;
					this.securityMediaNo =  this.tempData.securityMediaNo ;
					this.customerPhoneNumber =  this.tempData.customerPhoneNumber;
					this.customerEmail = this.tempData.customerEmail;
					this.securityMediaTypeCode = this.tempData.securityMediaTypeCode;
					this.isEditableBtnSendCode = true;
					this.isEditable = false;
					break;

				case "list":
					this.$router.push('/corporate-internet-banking/subscription')
					break;

				case "submit":{
					this.isIncorrectOTPCode = false;
					this.handleValidationMobileNumber(this.securityMediaNo)
					let validatecustomerEmailFormat = false;
					validatecustomerEmailFormat = this.$validate.validateEmail(this.customerEmail);
					
					if(this.securityMediaNo && validatecustomerEmailFormat && this.isAuthenticationCode && this.authenticationOTPSMSCode.authenticationCode){
						this.$dialog.onOpen({
							content:`${this.$t("message.would_you_like_to_save")}`,
							dialogMode: "Question",
							callback: ()=>{
								this.handleSubmit();
								modalController.dismiss();
							}
						})
					}else{
						this.handleValidateComformOTPCode();
						this.isEmptyAuthenticationCode = true;
						validatecustomerEmailFormat ? this.isWrongCustomerEmailFormat = false: this.isWrongCustomerEmailFormat = true;
						this.isInquiredInput = true;
					}
				}
					break;
			}
		},

		async handleSubmit() {
			let reqBody: CIB20100333_REQ_BODY = {
				userID: `${this.userID}`,
				email: this.customerEmail,
				customerNo: this.customerNo,
				cellPhoneNo: this.securityMediaNo,
				securityMediaNo: this.securityMediaNo,
				securityMediaTypeCode: this.securityMediaTypeCode
			};
			// ---- Adapter: CIB20100333_Adapter_IBSecurityMedia_Modify
			const responseData = await this.$request.getRequest("CIB20100333", reqBody, false, 'CIB201003SS03') as CIB20100333_RES;
			if(this.approvalType){
				this.isEditable = false;
				this.$emit("isReseTButton", false);
			}

			if (responseData.header.result) {
				this.isEditable = false;
				this.isCorrectOTPCode = false;
				this.isEmptyAuthenticationCode = false;
				this.isInquiredInput = false;
				this.authenticationOTPSMSCode.authenticationCode = ''
				this.isComfirmOTPSMSCodeButton = true
				this.isEditableBtnSendCode = true;
				this.$emit("isReseTButton", false);
				this.$dialog.onOpen({
					content:`${this.$t("message.submit_successfully")}`,
					callback:()=>{
						modalController.dismiss();
					}
				})
			} 
		},

		handleValidateComformOTPCode(){
			if(this.authenticationOTPSMSCode.authenticationCode && !this.isAuthenticationCode){
				this.isNotComfirmOTPCode = true;
			}else if(this.authenticationOTPSMSCode.authenticationCode && this.isAuthenticationCode){
				this.isNotComfirmOTPCode = false;
			}
		},

		handleValidationMobileNumber(phoneNumber: string){
			if(this.securityMediaTypeCode == '01'){
				if(phoneNumber.length > 0 && phoneNumber.length < 9){
					this.isWrongPhoneNumberFormat = true;
				}
				if(phoneNumber.length == 9 || phoneNumber.length > 9){
					this.isWrongPhoneNumberFormat = false;
				}
			}
		},

		async handleSendCodeToMobile() {
			this.authenticationOTPSMSCode.authenticationCode = '';
			this.isCorrectOTPCode = false;
			const reqBody: CIB20100222_REQ_BODY = {
				phoneNumber: this.securityMediaNo,
				userID: `${this.userID}`,
				countryID: 'Cambodia'
			};

			const responseData = await this.$request.getRequest('CIB20100222', reqBody, false) as CIB20100222_RES;

			if(responseData.header.result==true){
				this.isCountDown = true;
				this.authenticationOTPSMSCode.authTransactionDate = responseData.body.authTransactionDate;
				this.authenticationOTPSMSCode.authTransactionSeqNo = responseData.body.authTransactionSeqNo;
			}

			if(responseData.body.authTransactionSeqNo){
				this.isDisableComfirmOTPCode = false;
				this.isComfirmOTPSMSCodeButton = false;
			}
	
		},

		async handleConfirmOTPSMSCode(){
			const reqBody: CIB20100223_REQ_BODY = {
				authenticationCode: this.authenticationOTPSMSCode.authenticationCode,
				authTransactionDate: this.authenticationOTPSMSCode.authTransactionDate,
				authTransactionSeqNo: this.authenticationOTPSMSCode.authTransactionSeqNo
			};

			const responseData = await this.$request.getRequest('CIB20100223', reqBody, false) as CIB20100223_RES;
			
			if(responseData.body.successYN == 'Y'){
				this.isCountDown = false;
				this.isAuthenticationCode = true;
				this.isNotComfirmOTPCode = false;
				this.isIncorrectOTPCode = false;
				this.isCorrectOTPCode = true;
			}else{
				this.isIncorrectOTPCode = true;
				this.isNotComfirmOTPCode = false;
			}
		},

		handleStopCountdown(){
			this.isComfirmOTPSMSCodeButton = true;
		},
	},
});
