
import { PropType, defineComponent } from 'vue'
import { CustomInputEvent } from './lib/core'
import directive from './lib/directive'
import { Input, InputType } from './lib/format-input.type'

export default defineComponent({
    name: 'AccountInput',
    directives: {
        formatinput: directive
    },
    props: {
        inputType: {
            required: true,
            type: String as PropType<InputType>,
            default: 'currency'
        },
        modelValue: {
            type: [String, Number],
            required: true
        },
        value: {
            type: [String, Number]
        },
        placeholder: {
            type: String,
            default: ''
        },
        clearable: {
            type: Boolean,
            default: true
        },
        isReturnFormatValue: {
            type: Boolean,
            default: false
        },
        prefill: {
            type: Boolean,
            default: true
        },
        prefix: {
            type: String,
            default: ''
        },
        suffix: {
            type: String,
            default: ''
        },
        decimal: {
            type: String,
            default: '.'
        },
        separator: {
            type: String,
            default: ''
        },
        fractionDigits: {
            type: Number,
            default: 2
        },
        disabled: {
            type: Boolean,
            default: false
        }
    },
    emits: ['update:modelValue', 'input:modelValue', 'change', 'input'],
    data() {
        const formatValue = this.modelValue as Input | undefined
        const unformatValue = '' as Input | undefined
        const isClear = false
        return {
            formatValue,
            unformatValue,
            isClear
        }
    },
    watch: {
        modelValue(newValue, oldValue) {
            if (newValue !== oldValue) {
                this.formatValue = newValue
            }
        }
    },
    computed: {
        config() {
            let newConfig = {...this.$props}
            // Set default some props by type
            if(this.inputType === 'currency') {
                const separator = this.separator === '' ? ',' : this.separator
                newConfig = { ...this.$props, separator }

            } else if (this.inputType === 'account-number') {
                const separator = this.separator === '' ? ' ' : this.separator
                newConfig = { ...this.$props, separator }

            } else if (this.inputType === 'card-number') {
                const separator = this.separator === '' ? ' ' : this.separator
                newConfig = { ...this.$props, separator }

            } else if (this.inputType === 'phone-number') {
                const separator = this.separator === '' ? ' ' : this.separator
                const prefix = this.prefix === '' ? '0' : this.prefix
                newConfig = { ...this.$props, separator, prefix }
            } else if (this.inputType === 'expiry-date') {
                const separator = this.separator === '' ? '/' : this.separator
                newConfig = { ...this.$props, separator }
            }
            return newConfig
        },
        getInputType() {
            return this.inputType !== 'cvv' ? 'text' : 'password'
        },
        emittedValue() {
            return this.isReturnFormatValue ? this.formatValue : this.unformatValue
        },
    },
    methods: {
        input(event: Event) {
            this.isClear = false
            const { target } = event as CustomInputEvent
            this.formatValue = target.formatValue
            this.unformatValue = target.unformatValue
            this.$emit('input:modelValue', this.emittedValue)
            this.$emit('input', event)
        },
        change(event: Event) {
            // To wait for btn clear trigger or not
            setTimeout(() => {
                if(this.isClear) return
                this.$emit('update:modelValue', this.emittedValue)
                this.$emit('change', event)
            }, 100);
        },
        onClearValue() {
            this.isClear = true
            this.formatValue = ""
            this.unformatValue = ""
            this.$emit('input:modelValue', "")
            this.$emit('update:modelValue', "")
            const clearEvent = {
                target: { formatValue: '', unformatValue: ''}
            }
            this.$emit('change', clearEvent)
        }
    }
})
